.body{

    overflow-x: hidden;
}
.App-nostalgic{
    background-color: rgb(240, 231, 219);
    background-image: url(https://clecardona.com/summer_camp/eika/paper.jpeg);
    background-blend-mode: hard-light;
    
    .item {
        font-family:'Courier New', Courier, monospace;

        background: rgb(255, 255, 255);
      
        box-shadow: none;
        display: grid;
        grid-template-columns: 8px 1fr 60px;
        align-items: center;
        justify-items: center;
      
        margin: 6px 0;
        padding: 0px 0px;
        border-radius: none;
      
        .item-data {
          background: rgb(234, 238, 247);
          width: 100%;
          height: 100%;
      
          display: grid;
          grid-template-columns: 90px 1fr 60px 90px;
          align-items: center;
          justify-items: center;
        }
      
        img {
          position: absolute;
      
          height: 50px;
          width: 50px;
          object-fit:cover;
      
      
          border-radius: 30px;
          background: rgb(255, 255, 255);
          padding: 5px;
          border: 1px solid rgb(214, 214, 214);
        }
      
        .img-overlay {
          z-index: 2;
          position: relative;
          top: -2px;
          left: -2px;
          background-color: #14357e;
          color: white;
          height: 56px;
          width: 56px;
          border-radius: 30px;
          font-size: 35px;
      
          opacity: 0;
        }
        .img-overlay:hover {
          opacity: 1;
          transition: ease 0.3s;
        }
      
        .drawer {
          display: flex;
          flex-direction: row-reverse;
            
      
          .content {
         
      
              width: 80px;  
      
            display: flex;
            flex-direction: row;
            align-items: center;
            
      
          }
        }
      
        .lisere {
          background-color: none;
          height: 100%;
          width: 8px;
          border-radius: 5px 0 0 5px;
        }
      }



}

